import React, { useState } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators
} from 'reactstrap';

const items = [
  [
    {src: '/assets/Roma.jpg', name:"Rome"},
    {src: '/assets/Sevilla.jpg', name:"Seville"},
    {src: '/assets/Viena.jpg', name:"Vienna"},
    {src: '/assets/Amsterdam.jpg', name:"Amsterdam"}
  ],
  [
    {src: '/assets/Londres.jpg', name:"London"},
    {src: '/assets/Florencia.jpg', name:"Florence"},
    {src: '/assets/Lisboa.jpg', name:"Lisbon"},
    {src: '/assets/Barcelona.jpg', name:"Barcelona"}
  ],
  [
    {src: '/assets/Atenas.jpg', name:"Athens"},
    {src: '/assets/Edimburgo.jpg', name:"Edinburgh"},
    {src: '/assets/Estocolmo.jpg', name:"Stockholm"},
    {src: '/assets/Budapest_2.jpg', name:"Budapest"}
  ]
];

const MyCarousel = (props) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  }

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  }

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  }
  const slides = items.map((item, index) => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={index}
      >
        <div className="myCarousel">
            {item.map(city => {
              return (
                <div key={city.name} className="picture" style={{backgroundImage:`url(${city.src})`}}>
                  <h2>{city.name}</h2>
                </div>

              );
            })}
        </div>
      </CarouselItem>
    );
  });

  return (
    <div style={{backgroundColor:"#F6EBF9"}}>
      <h3>Popular MyTineraries</h3>
      <Carousel
        activeIndex={activeIndex}
        next={next}
        previous={previous}
      >
      <CarouselIndicators items={slides} activeIndex={activeIndex} onClickHandler={goToIndex} />
      {slides}
      <CarouselControl direction="prev" directionText=" " onClickHandler={previous} />
      <CarouselControl direction="next" directionText=" " onClickHandler={next} />
      </Carousel>
    </div>
  );
}

export default MyCarousel