import React, { useState } from 'react';
import { Nav, NavItem, Dropdown, DropdownItem, DropdownToggle, DropdownMenu} from 'reactstrap';
import { NavLink } from 'react-router-dom';

const Header = () => {
    const [dropdownOpen, setDropdownOpen] = useState(false);
    const toggle = () => setDropdownOpen(!dropdownOpen);

    return(
        <div className="navigator">
        <Nav pills>
            <NavItem>
                <NavLink to="/">Home</NavLink>
            </NavItem>
            <NavItem>
                <NavLink to="/cities">Cities</NavLink>
            </NavItem>
        </Nav>
        <Nav pills>
            <Dropdown nav isOpen={dropdownOpen} toggle={toggle}>
                <DropdownToggle nav caret>
                    <img className="user" src="../assets/user.png" alt="Sign in"/>
                </DropdownToggle>
                <DropdownMenu>
                    <DropdownItem><NavLink to="/login">Log In</NavLink></DropdownItem>
                    <DropdownItem><NavLink to="/signup">Sign Up</NavLink></DropdownItem>
                </DropdownMenu>
            </Dropdown>
        </Nav>
    </div>
    )
}

export default Header;